<template>
  <div class="p2">
    <P class="PP1" style="font-size:20px;height:35px; font-weight:bold;text-align: center;">
      《城建税、教育费附加、地方教育附加税（费）申报表》</P>
    <div>
      <p class="PP2">纳税人识别号：{{com.firm_code}}</p>

      <P class="PP3">所属时期起：{{com.month}}-01</P>

      <P class="PP4">所属时期止：{{com.month}}-30</P>

      <form>
        <table style="margin:0px auto; width:100%; text-align:center;" cellpadding="0" cellspacing="0" border="1">
          <tr>
            <td rowspan="3" colspan="2">纳税人信息</td>
            <td colspan="2">名称</td>
            <td colspan="3" style="background-color: #CCCCFF;">{{com.firm_name}}</td>
            <td colspan="2">登记类型</td>
            <td colspan="4" style="text-align: left;">
              <span style="float:left;">单位</span>
              <input type="radio" checked disabled
                     style="margin: 5px; width: 15px;height: 15px; float: left; display: block;">
              <span style="float:left;">个人</span>
              <input type="radio" disabled
                     style="margin: 5px; width: 15px;height: 15px; float: left; display: block;">
            </td>
          </tr>
          <tr>
            <td colspan="2">登记注册类型</td>
            <td style="background-color: #CCCCFF;">{{com.firm_type}}</td>
            <td>所属行业</td>
            <td style="background-color: #CCCCFF;">{{com.firm_trade}}</td>
            <td colspan="2">身份证件类型</td>
            <td colspan="4">居民身份证</td>
          </tr>
          <tr>
            <td colspan="2">身份证件号码</td>
            <td colspan="3" style="background-color: #CCCCFF;">{{com.firm_legal_person_id}}</td>
            <td colspan="2">联系方式</td>
            <td colspan="4" style="background-color: #CCCCFF;">{{com.firm_telephone}}</td>
          </tr>
          <tr>
            <td rowspan="4">征收项目</td>
            <td rowspan="4">征收品目</td>
            <td colspan="5">计税（费）依据</td>
            <td rowspan="3">税率（征收率）</td>
            <td rowspan="3">本期应纳税（费）额</td>
            <td rowspan="2" colspan="2">本期减免税（费）额</td>
            <td rowspan="3">本期已缴税（费）额</td>
            <td rowspan="3">本期应补（退）税（费）额</td>
          </tr>
          <tr>
            <td colspan="2">增值税</td>
            <td rowspan="2">消费税</td>
            <td rowspan="2">营业税</td>
            <td rowspan="2">合计</td>
          </tr>
          <tr>
            <td>一般增值税</td>
            <td>免抵税额</td>
            <td>减免性质代码</td>
            <td>减免额</td>
          </tr>
          <tr>
            <td>1</td>
            <td>2</td>
            <td>3</td>
            <td>4</td>
            <td>5=1+2+3+4</td>
            <td>6</td>
            <td>7=5*6</td>
            <td>8</td>
            <td>9</td>
            <td>10</td>
            <td>11</td>
          </tr>
          <tr>
            <td>城市维护建设税</td>
            <td>市区（增值税附征）</td>
            <td style="background-color:#D6EBFF;"><input v-model="form.a1_1" name="1_1" readonly/></td>
            <td style="background-color:#D6EBFF;"><input name="1_2" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="1_3" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="1_4" value="0.00"/></td>
            <td style="background-color: #CCCCFF;"><input name="1_5" value="0.00" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="1_6" value="0.07" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="1_7" v-model="form.a1_7" readonly/></td>
            <td style="background-color:#fff;"><input name="1_8" value=""/></td>
            <td style="background-color:#D6EBFF;"><input name="1_9" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="1_10" value="0.00"/></td>
            <td style="background-color: #CCCCFF;"><input name="1_11" v-model="form.a1_7" readonly/></td>
          </tr>
          <tr>
            <td>教育费附加</td>
            <td>增值税教育费附加</td>
            <td style="background-color:#D6EBFF;"><input v-model="form.a2_1" name="2_1" readonly/></td>
            <td style="background-color:#D6EBFF;"><input name="2_2" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="2_3" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="2_4" value="0.00"/></td>
            <td style="background-color: #CCCCFF;"><input name="2_5" value="0.00" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="2_6" value="0.03" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="2_7" v-model="form.a2_7" readonly/></td>
            <td style="background-color:#fff;"><input name="2_8" value=""/></td>
            <td style="background-color:#D6EBFF;"><input name="2_9" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="2_10" value="0.00"/></td>
            <td style="background-color: #CCCCFF;"><input name="2_11" v-model="form.a2_7" readonly/></td>
          </tr>
          <tr>
            <td>地方教育附加</td>
            <td>增值税地方教育附加</td>
            <td style="background-color:#D6EBFF;"><input :value="form.a3_1" name="3_1" readonly/></td>
            <td style="background-color:#D6EBFF;"><input name="3_2" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="3_3" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="3_4" value="0.00"/></td>
            <td style="background-color: #CCCCFF;"><input name="3_5" value="0.00" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="3_6" value="0.02" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="3_7" v-model="form.a3_7" readonly/></td>
            <td style="background-color:#fff;"><input name="3_8" value=""/></td>
            <td style="background-color:#D6EBFF;"><input name="3_9" value="0.00"/></td>
            <td style="background-color:#D6EBFF;"><input name="3_10" value="0.00"/></td>
            <td style="background-color: #CCCCFF;"><input name="3_11" v-model="form.a3_7" readonly/></td>
          </tr>
          <tr>
            <td colspan="2">合计</td>
            <td colspan="4">-</td>
            <td style="background-color: #CCCCFF;"><input name="4_5" value="0.00" readonly/></td>
            <td style="background-color: #CCCCFF;">-</td>
            <td style="background-color: #CCCCFF;"><input name="4_7" v-model="form.a4_7" readonly/></td>
            <td style="background-color: #CCCCFF;">-</td>
            <td style="background-color: #CCCCFF;"><input name="4_9" value="0.00" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="4_10" value="0.00" readonly/></td>
            <td style="background-color: #CCCCFF;"><input name="4_11" v-model="form.a4_7" readonly/></td>
          </tr>
        </table>
      </form>
      <div style="margin-top: 15px" class="uk-text-center">
        <button @click="saveForm" class="uk-button uk-button-primary"><i class="uk-icon-save"></i> 保存</button>
        <button class="uk-button uk-button-primary" @click="dataRestore()"><i class="uk-icon-close"></i> 删除</button>
        <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-file-excel-o"></i> 导出Excel
        </button>
        <button class="uk-button uk-button-primary" @click="useless()"><i class="uk-icon-print"></i> 打印</button>
      </div>
      <p style="color: red;text-align: left;font-size: 1.2rem;">
        温馨提示：<br><br>
        根据 《财政部 国家税务总局关于扩大有关政府性基金免征范围的通知》
        （财税〔2016〕12号）规定，从2016年2月1日起，月纳税营业额或销售额不超10万元（季纳税营业额或销售额不超过30万元）的缴纳义务人，
        免征教育费附加、地方教育附加。 由于营业税、增值税存在不同税率，无法根据当期税额自动计算申报表中“计税（费）依据”部分的内容，
        因此，在本申报表中申报“营业税教育费附加”“营业税地方教育附加”“增值税教育费附加”和“增值税地方教育费附加”时需手动选择减免。
        具体操作方式为：在“减免性质代码 列”选择“其他”，并在第9列录入减免金额。
      </p>
    </div>
  </div>
</template>

<script>
  import common from '../../mixins/common'

  export default {
    mixins: [common],
    data() {
      return {
        name: 36,  //当前表格的名称编号
        setting: null,
        form: {
          'a1_1': '0.00',
          'a2_1': '0.00',
          'a3_1': '0.00',
          'a1_7': '0.00',
          'a2_7': '0.00',
          'a3_7': '0.00',
          'a4_7': '0.00'
        },
        com: []
      }
    },
    computed: {
      formData() {
        return this.$store.state.tableData[this.name]
      }
    },
    mounted() {
      // let that = this;
      this.inputChangeEvent()
      this.formrevise()
      this.$nextTick(function () {
        this.inputClickEvent()
      })
      this.getSetting()  //获取后台设置数据

    },
    methods: {
      getSetting() {
        if (this.formData) {
          this.recoverFormData()
        }
        // //获取预设数据
        this.com = this.$store.state.companyCurrent.gscompany
        // this.setting = {'1_1': 10, '2_1': 5}
        this.initData()
      },
      initData() {
        this.$Loading.start()
        let that = this
        for (let key in this.setting) {
          J('input').each(function () {
            if (J(this).attr('name') === key) {
              J(this).val((that.setting[key] * 1).toFixed(2))
              J(this).trigger('blur')
              return false
            }
          })
        }
        this.$Loading.finish()
      },
      recoverFormData() {
        this.$Loading.start()
        let that = this
        J('input').each(function () {
          let nm = J(this).attr('name')
          that.formData.map(v => {
            if (v.name === nm) {
              J(this).val(v.value)
            }
          })
        })
        this.$Loading.finish()
      },
      fn() {
        this.fn1(5, 3, [1, 2, 3], [1, 2, 3], 1)
        this.fn1(7, 3, [5, 6], [1, 2, 3], 2)
        this.fn3()
        this.fn2()
      },
      fn1(q, r, seed, rows, type) {
        for (let i = 1; i <= r; i++) {
          let sum = 0
          for (let j = 0; j < seed.length; j++) {
            let m = seed[j]
            let dom = J('input[name=' + i + '_' + m + ']')
            if (J.inArray(i, rows) >= 0) {
              if (dom.val()) {
                if (!isNaN(dom.val())) {
                  if (type === 1) {
                    sum += parseFloat(dom.val())
                  } else if (type === 2) {
                    if (j === 0) {
                      sum = parseFloat(dom.val())
                    } else {
                      sum *= parseFloat(dom.val())
                    }
                  }
                }
              }
            }
          }
          if (type === 1) {
            J('input[name=' + i + '_' + q + ']').val(sum.toFixed(2))
          } else if (type === 2) {
            this.form['a' + i + '_' + q] = sum.toFixed(2)
          }
        }
      },
      fn2() {
        setTimeout(() => {
          let dom1 = J('input[name=1_7]').val() * 1
          let dom2 = J('input[name=2_7]').val() * 1
          let dom3 = J('input[name=3_7]').val() * 1
          let sum = (dom1 + dom2 + dom3).toFixed(2)
          this.form.a4_7 = sum
        }, 150)
      },
      fn3() {
        let dom1 = J('input[name=1_5]').val() * 1
        let dom2 = J('input[name=2_5]').val() * 1
        let dom3 = J('input[name=3_5]').val() * 1
        let sum = (dom1 + dom2 + dom3).toFixed(2)
        J('input[name=4_5]').val(sum)
      },
      formrevise() {
        var name1 = ''
        if (this.$store.state.tableData[27]) {
          this.form.a1_1 = this.$store.state.tableData[27][106].value
          this.form.a2_1 = this.$store.state.tableData[27][106].value
          this.form.a3_1 = this.$store.state.tableData[27][106].value
        } else {
          name1 = '增值税纳税申报表（适用于增值税一般纳税人）'
        }
        if (name1 != '') {
          this.eg('请按照填表顺序正确填写:' + name1 + '表未填写！')
        }
      }
    }
  }
</script>

<style scoped>
  table, table tr th, table tr td {
    font-family: '宋体';
    border: 1px solid #333;
  }

  table {
    min-height: 25px;
    line-height: 25px;
    text-align: center;
    border-collapse: collapse;
    padding: 2px;
  }

  input, select {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
    padding: 0 2px;
    outline: 0px;
    text-align: right;
  }

  td {
    text-align: center;
    padding: 3px 2px;
  }

</style>



